.leftAlign{
    text-align: left;
}

.upperCase{
    text-transform: uppercase;
}
.colorRed{
    color: #c56161;
}
.Collapsible__trigger{
    font-weight: bold;
    cursor: pointer;
}

.Collapsible__trigger:hover{
    background-color: #fed136;
}

.Projects .paddBottom1em{
    padding-bottom: 1em;
}
.NavBarResume{
    max-width: 1920px;
    margin: 0 auto;
}
.noMarginBottom{
    margin-bottom: 0;
}
.Resume{
  margin-top:1em;
}

/* hover state over projects */
.Collapsible__trigger {
  position: relative;
  display: inline-block;
  text-decoration: underline;
}
